<template>
  <div>
    <section-container class="login-form" header-text="Login">
      <form @submit.prevent>
        <div v-show="error" class="form-message form-error-message">{{ error }}</div>

        <div class="input-group">
          <label for="email">Email</label>
          <input v-model="$v.user.email.$model" id="email" type="text" :class="{ 'input-error': $v.user.email.$error }" class="input-base" placeholder="example@gmail.com">
        </div>

        <div class="input-group">
          <label>Password</label>
          <password-input v-model="$v.user.password.$model" :input-class=" { 'input-error': $v.user.password.$error }" id="password"></password-input>
        </div>

        <div class="forgot-password-container">
          <router-link to="/password/forgot" class="forgot-password-link">Don’t remember password?</router-link>
        </div>

        <button :disabled="$v.$invalid" @click="submit" type="submit" class="btn-primary submit-btn">Continue</button>
      </form>
    </section-container>
    <section-container class="register-link-container">
      <span class="register-or-login-text">I have no account, <router-link to="/register">REGISTER NOW</router-link></span>
    </section-container>
  </div>
</template>

<script>
import SectionContainer from '@/components/SectionContainer'
import { mapActions } from 'vuex'
import { alphaNum, email, maxLength, minLength, required } from 'vuelidate/lib/validators'
import PasswordInput from '@/components/PasswordInput'

export default {
  name: 'Login',
  components: { SectionContainer, PasswordInput },
  data () {
    return {
      error: null,
      user: {
        email: '',
        password: ''
      }
    }
  },
  validations: {
    user: {
      email: {
        email,
        required,
        maxLength: maxLength(32)
      },
      password: {
        required,
        minLength: minLength(6),
        maxLength: maxLength(24),
        alphaNum
      }
    }
  },
  methods: {
    ...mapActions({
      login: 'login'
    }),
    submit () {
      this.error = "";

      if (!this.$v.invalid) {
        const resolve = () => this.$router.push('/')

        this.login({ ...this.user })
          .then(resolve)
          .catch((error) => {
            this.error = error.message;
          })
      }
    }
  }
}
</script>

<style lang="scss" scoped>

.login-form {
  margin-top: 70px;
}

.forgot-password-container {
  margin: 5px 0 16px 0;
  display: flex;
  justify-content: flex-end;

  .forgot-password-link {
    color: #8C8C8C;
    text-decoration: none;
  }
}

@include breakpoint-to(445px) {
  .login-form, .register-link-container {
    margin-left: 10px;
    margin-right: 10px;
  }
}

</style>
