<template>
  <div class="password-input-container">
    <input
      :id="id"
      :type="type"
      :class="inputClass"
      @input="$emit('update', $event.target.value)"
      class="input-base password-input"
    >

    <span @click="togglePasswordVisibility" class="show-password-icon" :class="{ 'hide-password': isShow }"></span>
  </div>
</template>

<script>
export default {
  name: 'PasswordInput',
  props: {
    inputClass: '',
    value: '',
    id: {
      required: true,
      type: String
    }
  },
  model: {
    prop: "value",
    event: "update"
  },
  data() {
    return {
      isShow: false,
      type: "password"
    }
  },
  methods: {
    togglePasswordVisibility() {
      if (this.type === "password") {
        this.type = "text";
        this.isShow = true;
      } else {
        this.type = "password";
        this.isShow = false;
      }
    }
  }
}
</script>

<style lang="scss" scoped>

.password-input-container {
  position: relative;

  .icon-show-password {
    position: absolute;
    display: inline;
    width: 24px;
    height: 16px;
    right: 18px;
    bottom: 20px;

    background-image: url("../assets/svg/view-icon.svg");

    &.hide-password {
      background-image: url("../assets/svg/hide-icon.svg");
    }
  }
}

</style>
